import React, { useEffect, useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Loader2, Mail, ExternalLink } from 'lucide-react';
import { toast } from 'sonner';
import axiosInstance from '@/api/axios/axiosInstance';
import GmailOAuthModal from '@/components/GmailOAuthModal';

interface GmailActionProps {
  card: any;
  capturedData: any;
  updateGmailConfig: (cardId: number, field: any, value: any) => void;
  updateActionCard: (cardId: number, updates: any) => void;
  gmailAuthStatus: 'disconnected' | 'connecting' | 'connected';
  setGmailAuthStatus: (status: 'disconnected' | 'connecting' | 'connected') => void;
}

const GmailAction: React.FC<GmailActionProps> = ({
  card,
  capturedData,
  updateGmailConfig,
  updateActionCard,
  gmailAuthStatus,
  setGmailAuthStatus
}) => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [profile, setProfile] = useState<any>(null);

  const checkConnection = async () => {
    try {
      // Use the same endpoint as Integrations page
      const response = await axiosInstance.get('/api/integration-gmail/auth/status');
      if (response.data.success && response.data.data.isConnected) {
        setGmailAuthStatus('connected');
        setProfile(response.data.data.profile);
        
        // Update action card if not already marked as connected
        if (!card.isConnected) {
          updateActionCard(card.id, { 
            isConnected: true,
            gmailConfig: {
              ...card.gmailConfig,
              fromEmail: response.data.data.profile?.email || ''
            }
          });
        }
      } else {
        setGmailAuthStatus('disconnected');
      }
    } catch (error) {
      console.error('Error checking Gmail connection:', error);
      setGmailAuthStatus('disconnected');
    }
  };

  useEffect(() => {
    checkConnection();
  }, []);

  const handleConnect = () => {
    setIsModalOpen(true);
  };

  const handleSuccess = (data: any) => {
    setGmailAuthStatus('connected');
    setProfile(data);
    updateActionCard(card.id, { 
      isConnected: true,
      gmailConfig: {
        ...card.gmailConfig,
        fromEmail: data.email || ''
      }
    });
    setIsModalOpen(false);
    toast.success('Gmail connected successfully!');
  };

  return (
    <div className="space-y-4">
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-2">
          <Mail className="h-5 w-5 text-red-500" />
          <span className="font-semibold text-white text-lg">Gmail Integration</span>
        </div>
        {gmailAuthStatus === 'connected' && profile && (
          <span className="text-xs text-green-400 bg-green-400/10 px-2 py-1 rounded-full border border-green-400/20">
            Connected: {profile.email}
          </span>
        )}
      </div>

      {!card.isConnected ? (
        <Button
          onClick={handleConnect}
          disabled={gmailAuthStatus === 'connecting'}
          className="w-full h-12 bg-blue-600 hover:bg-blue-700 text-white rounded-lg flex items-center justify-center gap-2 transition-all"
        >
          {gmailAuthStatus === 'connecting' ? (
            <Loader2 className="h-5 w-5 animate-spin" />
          ) : (
            <Mail className="h-5 w-5" />
          )}
          {gmailAuthStatus === 'connecting' ? 'Connecting...' : 'Connect Gmail Account'}
        </Button>
      ) : (
        <div className="p-4 bg-green-500/10 border border-green-500/20 rounded-lg flex items-center justify-between">
          <div className="flex items-center gap-2">
            <div className="w-2 h-2 bg-green-500 rounded-full animate-pulse"></div>
            <span className="text-sm font-medium text-green-400">Connected to Gmail</span>
          </div>
          <Button 
            variant="ghost" 
            size="sm" 
            onClick={handleConnect}
            className="text-xs text-blue-400 hover:text-blue-300 hover:bg-blue-400/10"
          >
            Switch Account
          </Button>
        </div>
      )}

      {card.isConnected && (
        <div className="mt-6 space-y-4 p-5 bg-white/5 rounded-xl border border-white/10 backdrop-blur-sm">
          <div className="flex items-center gap-2 mb-2">
            <h4 className="text-sm font-semibold text-white/80 uppercase tracking-wider">Email Configuration</h4>
          </div>

          {capturedData && (
            <div className="mb-4 p-3 bg-blue-500/10 border border-blue-400/30 rounded-lg flex items-start gap-3">
              <div className="h-5 w-5 rounded-full bg-blue-500/20 flex items-center justify-center flex-shrink-0 mt-0.5">
                <span className="text-[10px] text-blue-400 font-bold">i</span>
              </div>
              <div>
                <p className="text-xs text-blue-300 font-medium">Webhook Data Available</p>
                <p className="text-[10px] text-blue-300/60 mt-0.5">
                  Use curly braces like <code className="bg-blue-500/20 px-1 rounded">{"{{field}}"}</code> to insert dynamic values.
                </p>
              </div>
            </div>
          )}

          <div className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <Label className="text-xs font-semibold text-white/60 mb-1.5 block">From Email</Label>
                <Input
                  value={card.gmailConfig?.fromEmail || profile?.email || ''}
                  readOnly
                  className="w-full h-11 bg-white/5 border-white/10 text-white/50 focus:ring-0 cursor-not-allowed text-sm"
                />
              </div>

              <div>
                <Label className="text-xs font-semibold text-white/60 mb-1.5 block">To Recipient</Label>
                <div className="relative">
                  <Input
                    value={card.gmailConfig?.to || ''}
                    onChange={(e) => updateGmailConfig(card.id, 'to', e.target.value)}
                    placeholder="e.g. {{email}} or user@example.com"
                    className="w-full h-11 bg-white/5 border-white/15 text-white placeholder:text-white/20 focus:border-blue-500/50 transition-all text-sm pr-10"
                  />
                  {capturedData && (
                    <div className="absolute right-1 top-1/2 -translate-y-1/2">
                      <Select onValueChange={(value) => updateGmailConfig(card.id, 'to', value)}>
                        <SelectTrigger className="w-8 h-8 p-0 border-0 bg-transparent hover:bg-white/10 focus:ring-0">
                          <span className="text-blue-400 font-bold text-lg">+</span>
                        </SelectTrigger>
                        <SelectContent className="bg-[#0b0f2e] border-white/20 text-white max-h-60">
                          {Object.keys(capturedData).map((key) => (
                            <SelectItem key={key} value={`{{${key}}}`} className="focus:bg-white/10 focus:text-white">
                              {key}
                            </SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                    </div>
                  )}
                </div>
              </div>
            </div>

            <div>
              <Label className="text-xs font-semibold text-white/60 mb-1.5 block">Subject</Label>
              <Input
                value={card.gmailConfig?.subject || ''}
                onChange={(e) => updateGmailConfig(card.id, 'subject', e.target.value)}
                placeholder="Email Subject (use {{field}} for dynamic data)"
                className="w-full h-11 bg-white/5 border-white/15 text-white placeholder:text-white/20 focus:border-blue-500/50 transition-all text-sm"
              />
            </div>

            <div>
              <Label className="text-xs font-semibold text-white/60 mb-1.5 block">Email Body (HTML supported)</Label>
              <textarea
                value={card.gmailConfig?.body || ''}
                onChange={(e) => updateGmailConfig(card.id, 'body', e.target.value)}
                placeholder="Write your email content here..."
                className="w-full min-h-[180px] p-4 bg-white/5 border border-white/15 text-white placeholder:text-white/20 rounded-lg focus:outline-none focus:border-blue-500/50 transition-all text-sm resize-y"
              />
            </div>
          </div>
        </div>
      )}

      <GmailOAuthModal
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        onSuccess={handleSuccess}
        useIntegrationApi={true}
      />
    </div>
  );
};

export default GmailAction;
