import React, { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { toast } from 'sonner';
import axiosInstance from '@/api/axios/axiosInstance';

interface ListsActionProps {
  card: any;
  availableLists: any[];
  selectedList: string;
  setSelectedList: (listId: string) => void;
  isCreatingList: boolean;
  setIsCreatingList: (creating: boolean) => void;
  newListData: any;
  setNewListData: (data: any) => void;
  handleCreateList: () => Promise<void>;
  updateActionCard: (cardId: number, updates: any) => void;
  webhookCreated: boolean;
  workflowEnabled: boolean;
  webhookUrl: string;
  onClose: () => void;
}

const ListsAction: React.FC<ListsActionProps> = ({
  card,
  availableLists,
  selectedList,
  setSelectedList,
  isCreatingList,
  setIsCreatingList,
  newListData,
  setNewListData,
  handleCreateList,
  updateActionCard,
  webhookCreated,
  workflowEnabled,
  webhookUrl,
  onClose
}) => {
  return (
    <div className="space-y-6">
      <div>
        <h3 className="text-lg font-semibold mb-4 text-white">Lists Configuration</h3>
        <p className="text-white/60 mb-4">
          Configure how webhook data will be stored in your contact lists.
        </p>
      </div>

      <div className="space-y-4">
        {!isCreatingList ? (
          <div>
            <label className="block text-sm font-medium text-white mb-2">
              Select List
            </label>
            <Select value={selectedList} onValueChange={(value) => {
              if (value === 'create-new') {
                setIsCreatingList(true);
              } else {
                setSelectedList(value);
              }
            }}>
              <SelectTrigger className="w-full h-12 border border-white/20 bg-white/5 text-white rounded-lg">
                <SelectValue placeholder="Choose a list or create new..." />
              </SelectTrigger>
              <SelectContent className="bg-[#0b0f2e] border-white/20">
                {availableLists && availableLists.length > 0 ? (
                  availableLists.map((list) => (
                    <SelectItem key={list.id} value={list.id.toString()} className="p-3 text-white hover:bg-white/10">
                      <div>
                        <div className="font-medium">{list.list_name}</div>
                        <div className="text-sm text-white/50">
                          {list.contactCount || 0} contacts
                        </div>
                      </div>
                    </SelectItem>
                  ))
                ) : (
                  <SelectItem value="no-lists" disabled className="p-3">
                    <div className="text-sm text-white/50">No lists available</div>
                  </SelectItem>
                )}
                <SelectItem value="create-new" className="p-3 border-t border-white/10 text-white hover:bg-white/10">
                  <div className="font-medium text-blue-400">+ Create New List</div>
                </SelectItem>
              </SelectContent>
            </Select>
          </div>
        ) : (
          <div className="space-y-4 p-4 border border-white/20 rounded-lg bg-white/5">
            <div className="flex items-center justify-between">
              <h4 className="text-sm font-medium text-white">Create New List</h4>
              <Button
                variant="ghost"
                size="sm"
                className="text-white/60 hover:text-white hover:bg-white/10"
                onClick={() => {
                  setIsCreatingList(false);
                  setNewListData({
                    listName: '',
                    description: '',
                    type: 'General'
                  });
                }}
              >
                Cancel
              </Button>
            </div>

            <div className="space-y-3">
              <div>
                <Label className="text-xs text-white/60 mb-1">List Name *</Label>
                <Input
                  value={newListData.listName}
                  onChange={(e) => setNewListData({...newListData, listName: e.target.value})}
                  placeholder="Enter list name"
                  className="bg-white/5 border-white/20 text-white"
                />
              </div>
              <div>
                <Label className="text-xs text-white/60 mb-1">Description *</Label>
                <Input
                  value={newListData.description}
                  onChange={(e) => setNewListData({...newListData, description: e.target.value})}
                  placeholder="Enter description"
                  className="bg-white/5 border-white/20 text-white"
                />
              </div>
            </div>

            <Button
              onClick={handleCreateList}
              className="w-full mt-4 bg-blue-600 hover:bg-blue-700 text-white"
            >
              Create List
            </Button>
          </div>
        )}

        {/* Field Mapping Preview */}
        <div>
          <label className="block text-sm font-medium text-white mb-2">
            Field Mapping
          </label>
          <div className="space-y-2">
            <div className="flex items-center justify-between p-3 border border-white/10 rounded-lg bg-white/5 text-xs">
              <span className="text-white/80">Email</span>
              <span className="text-white/40">‚Üí email</span>
            </div>
            <div className="flex items-center justify-between p-3 border border-white/10 rounded-lg bg-white/5 text-xs">
              <span className="text-white/80">Phone</span>
              <span className="text-white/40">‚Üí phoneNumber</span>
            </div>
          </div>
        </div>

        <div className="pt-6">
          <Button
            onClick={async () => {
              if (!selectedList && !isCreatingList) {
                toast.error('Please select a list first');
                return;
              }

              try {
                if (!webhookCreated || !workflowEnabled) {
                  toast.error('Please activate the webhook first');
                  return;
                }

                if (webhookUrl) {
                  const webhookId = webhookUrl.split('/').pop();
                  await axiosInstance.put(`/api/webhooks/${webhookId}/config`, {
                    listId: selectedList,
                    appType: 'lists'
                  });
                }

                updateActionCard(card.id, {
                  isConnected: true,
                  selectedListId: selectedList
                });

                toast.success('Lists configuration saved!');
                onClose();
              } catch (error: any) {
                console.error('Error saving configuration:', error);
                toast.error(error.response?.data?.message || 'Failed to save configuration');
              }
            }}
            className="w-full h-12 bg-green-600 hover:bg-green-700 text-white rounded-lg"
          >
            Save Configuration
          </Button>
        </div>
      </div>
    </div>
  );
};

export default ListsAction;
